import React, {useEffect} from "react";
import {busData} from "../../../db/busData";
import * as S from "./style";

declare global {
  interface Window {
    kakao: any;
  }
}
const Map = () => {
  //map 
  useEffect(() => {
    let mapContainer = document.getElementById("map");
    let mapOption = {
      center: new window.kakao.maps.LatLng(
        37.466806528974935,
        126.93216211295136
      ),
      level: 3,
    };

    let map = new window.kakao.maps.Map(mapContainer, mapOption);

    busData.forEach(el => {
      new window.kakao.maps.Marker({
        // 
        map: map,
        // 
        position: new window.kakao.maps.LatLng(el.lat, el.lng),
        //
        title: el.title,
      });
    });

    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(function (position: any) {
        let lat = position.coords.latitude; //
        let lon = position.coords.longitude; //

        let locPosition = new window.kakao.maps.LatLng(lat, lon),
          message = '<div style="padding:5px"> </div>';

        displayMarker(locPosition, message);
      });
    } else {
      let locPosition = new window.kakao.mpas.LatLng(
          37.466806528974935,
          126.93216211295136
        ),
        message = "geolocation   .";
    }

    function displayMarker(locPosition: any, message: any) {
      let marker = new window.kakao.maps.Marker({
        map: map,
        position: locPosition,
        clickable: true, //marker     
      });

      let iwContent = message;
      let iwRemoveable = true;

      let infowindow = new window.kakao.maps.InfoWindow({
        content: iwContent,
        removable: iwRemoveable,
      });

      window.kakao.maps.event.addListener(marker, "click", function () {
        infowindow.open(map, marker);
      });

      map.setCenter(locPosition);
      marker.setMap(map);
    }
  }, []);

  return (
    <S.MapContainer>
      <div
        id="map"
        style={{width: "200vh", height: "100vh", margin: "0 auto"}}
      ></div>
      <p id="data"></p>
    </S.MapContainer>
  );
};

export default Map;
